<template>
  <!-- 左侧导航栏 -->
  <!-- background-color="#545c64" -->
  <div id="leftAside">
    <el-menu
      :default-active="selectType"
      class="el-menu-vertical-demo"
      @select="handleSelect"
      text-color="#333"
      active-text-color="#ffd04b"
      :collapse="false"
    >
      <el-menu-item
        :index="item.navRouter"
        v-for="(item, index) in navList"
        :key="item.navRouter + index"
      >
        <template slot="title">
          <i :class="'el-icon-folder-opened'"></i>
          <span>{{ item.value }}</span>
        </template>
      </el-menu-item>
    </el-menu>
  </div>
</template>

<script>
// import { rotate } from '@antv/x6/lib/util/dom/transform'
export default {
  data() {
    return {
      navList: [
        {
          index: "0",
          navRouter: "image",
          value: "图片",
          icon: "el-icon-menu",
        },
        {
          index: "1",
          navRouter: "text",
          value: "文本",
          icon: "el-icon-document",
        },
        {
          index: "2",
          navRouter: "video",
          value: "视频",
          icon: "el-icon-setting",
        },
        {
          index: "3",
          navRouter: "audio",
          value: "音频",
          icon: "el-icon-setting",
        },
        {
          index: "4",
          navRouter: "remote_sensing",
          value: "遥感",
          icon: "el-icon-setting",
        },
      ],
      // 二级导航栏列表
      sendData: [],
      // 刷新展示类型
      selectType: "",
    };
  },
  computed: {
    // 选中的一级菜单类型
    currentType() {
      return this.$store.state.ItemDetails.currentType;
    },
    // // 选中的二级菜单
    // currentName() {
    //   return this.$store.state.ItemDetails.itemDate;
    // },
  },
  watch: {
    currentType: {
      handler(newValue) {
        // console.log(newValue);
        this.selectType = newValue;
        // window.localStorage.setItem("reloadType ", JSON.stringify(newValue));

        // this.$store.commit("ItemDetails/currentType", newValue);
      },
    },
  },

  methods: {
    //  默认获取图片对应的二级菜单列表信息
    getResultList() {
      console.log("刷新后一级菜单类型");
      // console.log(this.currentType);
      if (this.currentType == "") {
        this.selectType = "image";
        this.$store.commit("ItemDetails/currentType", this.selectType);
        // console.log(this.currentType);
      } else {
        this.selectType = this.currentType;
      }
    },
    handleSelect(key) {
      // console.log(key);
      // 获取对应二级菜单信息列表
      this.$store.commit("ItemDetails/currentType", key);
      this.$router.push({ name: key });
    },
  },
  created() {
    this.getResultList();
  },
};
</script>

<style lang="scss" scoped>
#leftAside {
  overflow: hidden;
  .el-menu {
    height: 100%;
    width: 100%;
  }
}
</style>
