<template>
  <div id="ItemDetails">
    <el-container>
      <!-- <el-aside width="180px">
        <div class="leftaside">
          <el-header><div class="title">MoFarm</div></el-header>
          <el-row>
            <el-col :span="24">
              <div class="left">
                <con-aside></con-aside>
              </div>
            </el-col>
            <el-col :span="12">
              <div class="right">
                <result-list></result-list>
              </div>
            </el-col>
          </el-row>
        </div>
      </el-aside> -->
      <el-main>
        <div class="content">
          <keep-alive>
            <router-view />
          </keep-alive>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import ConAside from "./DetailsComponent/CommonAside";
// import ConCard from './DetailsComponent/CommonCard'
import ResultList from "./DetailsComponent/ResultList";
// import ImagesDetails from './Childrens/ImagsDetails.vue'
// import TextsDetails from './Childrens/TextsDetails.vue'

export default {
  // name: "Item",
  // components: { ConAside, ResultList },
  data() {
    return {
      //  key: [],
    };
  },
  computed: {
    currentType() {
      return this.$store.state.ItemDetails.currentType;
    },
  },
  methods: {
    beforeunloadFn() {
      var reLoadType = localStorage.getItem("reLoadType");
      this.currentType = reLoadType;
    },
    //刷新页面时保存vuex中的信息
    saveVuexToLocal() {
      // const that = this;
      if (sessionStorage.getItem("store")) {
        this.$store.replaceState(
          Object.assign(
            {},
            this.$store.state,
            JSON.parse(sessionStorage.getItem("store"))
          )
        );
        localStorage.removeItem("store");
      }
      console.log(this.saveVuex);
      window.addEventListener("beforeunload", this.saveVuex);
    },
    //页面退出时,取消监听
    cancelListening() {
      window.removeEventListener("beforeunload", this.saveVuex);
      sessionStorage.removeItem("store");
    },
    //保存vuex中的数据
    saveVuex() {
      sessionStorage.setItem("store", JSON.stringify(this.$store.state));
    },
  },
  mounted() {},
  created() {
    this.saveVuexToLocal(); //保存vuex信息刷新不丢失
  },
  deactivated() {
    this.cancelListening(); //页面退出时,取消监听
  },
  beforeDestroy() {
    // this.cancelListening(); //页面退出时,取消监听
  },
};
</script>

